import { useStripe } from "@stripe/react-stripe-js";
import { authLogout, useDbContext, useUserContext } from "../context/AuthProvider";
import StripeConfig from '../config/stripe.config.json'

export default function Account() {
    const user = useUserContext();
    const db = useDbContext();
    const stripe = useStripe();
    const handleLogout = () => {
        authLogout();
    };
    const handleDeleteAccount = () => {
        if (user) {
            user.delete().then(() => console.log('Successfully deleted account')).catch((e) => console.error(e));
        }
    };
    const handleStripeBasicSubscription = async () => {
        if (user) {
            const docRef = await db.collection('customers').doc(user.uid).collection('checkout_sessions').add({
                price: StripeConfig.subscription_prices_keys.basic,// priceId from stripe
                success_url: window.location.origin, // return user to this screen
                cancel_url: window.location.origin, // return user to this screen
            });
            docRef.onSnapshot((snap) => {
                const data = snap.data();
                if (data) {
                    const { error, sessionId } = data;
                    if (error) {
                        alert('An error occured: ' + error.message);
                    }
                    if (sessionId) {
                        if (stripe) {
                            stripe.redirectToCheckout({ sessionId })
                        }
                    }
                }
            });

        }
    }
    const handlePermissionTest = async () => {
        if (user) {
            const docRef = await db.collection('customers').doc(user.uid).collection('checkout_sessions').add({
                price: StripeConfig.subscription_prices_keys.basic,// priceId from stripe
                success_url: window.location.origin, // return user to this screen
                cancel_url: window.location.origin, // return user to this screen
            });
            if (docRef) {
                console.log('success?')
            } else {
                console.log('failure?')
            }
        }
    }
    return <div>
        <div>Welcome {user?.displayName} !</div>
        <div>Welcome {user?.uid} !</div>
        <div>
            <button onClick={handlePermissionTest} style={{ color: 'blue' }}>Permissions?</button>
        </div>
        <div>
            <button onClick={handleStripeBasicSubscription} style={{ color: 'green' }}>Stripe Subscribe</button>
        </div>
        <div>
            <button onClick={handleLogout} style={{ color: 'orange' }}>Logout</button>
        </div>
        <div>
            <button onClick={handleDeleteAccount} style={{ color: 'red' }}>Delete Account</button>
        </div>
    </div>
}